import React, { useEffect, useState } from "react";
import { useLocation } from "react-router";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/components/prism-bash";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post28() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["azure", "firewall"];

  const configurePrismTheme = (themeMode) => {
    let link = document.getElementById("prism-theme");

    if (!link) {
      link = document.createElement("link");
      link.rel = "stylesheet";
      link.id = "prism-theme";
      document.head.appendChild(link);
    }

    link.href =
      themeMode === "dark"
        ? "https://unpkg.com/prismjs/themes/prism-twilight.css"
        : "https://unpkg.com/prismjs/themes/prism.css";
  };

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
      configurePrismTheme("dark");
    } else {
      setThemeMode("light");
      configurePrismTheme("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
      configurePrismTheme(location.state.themeMode);
    } else {
      configurePrismTheme("light");
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <>
      <Helmet>
        <title>Azure Firewall Manual Updates and IaC</title>
        <meta
          name="description"
          content="Azure Firewall Manual Updates and IaC"
        />
        <meta name="keywords" content="azure, postgresql, wal-g" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta
          property="og:title"
          content="Azure Firewall Manual Updates and IaC"
        />
        <meta
          property="og:description"
          content="Azure Firewall Manual Updates and IaC"
        />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom6">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-3xl lg:text-4xl w-full md:w-full dark:text-gray-custom1">
                Azure Firewall Manual Updates and IaC
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Oct 26, 2025
                </time>
              </span>
              <div className="line-break"></div>
              <div
                className={
                  themeMode === "dark"
                    ? "dark articleBody"
                    : "light articleBody"
                }
              >
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Azure Firewall is the native firewall offering in Microsoft
                  Azure. While firewall rules can be configured directly on the
                  Azure Firewall resource, Microsoft recommends using the Azure
                  Firewall Policy resource for rule management. Azure Firewall
                  Policy is a globally available resource that enables
                  centralized rule definition and can be shared across multiple
                  Azure Firewall instances, including those deployed in Secured
                  Virtual Hubs and Hub Virtual Networks [1].
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Azure Firewall Policy supports network rules, application
                  rules, and NAT rules. It introduces an abstraction called rule
                  collection, which groups rules of a specific type (e.g.,
                  network or application). Additionally, rule collection groups
                  allow you to organize one or more rule collections within an
                  Azure Firewall Policy, enabling structured and prioritized
                  rule management.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Azure Firewall Policy rules can be managed using the Azure
                  Portal, Azure CLI, Azure PowerShell, and Infrastructure as
                  Code (IaC) tools such as ARM templates and Terraform. In this
                  article, I will describe one application I created to simplify
                  some operations related to Azure Firewall Policies.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The following repository contains a tool for the conversion of
                  the ARM Template of Azure Firewall Policies to Terraform code:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/arm-template-terraform-conversion"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/arm-template-terraform-conversion
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Managing firewall rules through Infrastructure as Code (IaC)
                  is generally recommended, as it enables change tracking and
                  provides a reliable backup of the configuration. However,
                  there are situations where making manual changes via the Azure
                  Portal can be more practical, for example, during the initial
                  migration of firewall rules to Azure Firewall, when a faster,
                  and flexible method is needed to validate the rules in the
                  cloud environment.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  This tool is designed to simplify the conversion of manually
                  applied rules in an Azure Firewall Policy into their
                  equivalent Terraform configuration. The tool is particularly
                  useful because comparing Terraform defined rules with those
                  configured directly in Azure Firewall Policy is often time
                  consuming. Additionally, keeping the Terraform code aligned
                  with the actual firewall rules can be error prone without
                  automation.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The tool is a Bash script that takes the path of an ARM
                  template file containing the definition of an Azure Firewall
                  Policy, and generates the corresponding Terraform code. It
                  creates one Terraform module for each firewall rule collection
                  group defined in the Azure Firewall Policy.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Execute the BASH script with the following parameters:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-none">
                    ./firewall-policy-conversion.sh -p
                    path_to_firewall_policy_arm_template.json
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  An optional file named <i>ip_groups_mapping.csv</i> can be
                  placed in the same directory as the Bash script. This file
                  contains the mapping between the parameter names of the IP
                  Groups in the ARM template of the Azure Firewall Policy and
                  the corresponding variable names of the IP Groups defined in
                  the Terraform project.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The file <i>ip_groups_mapping.csv</i> can be generated using
                  the Bash script <i>ip-groups-mapping.sh</i>, which utilizes
                  Azure CLI to extract the names of IP Groups based on the
                  provided input parameter Subscription ID.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [1]{" "}
                  <a
                    className="underline"
                    href="https://learn.microsoft.com/en-us/azure/firewall/policy-rule-sets"
                    target="_blank"
                    rel="noreferrer"
                  >
                    azure/firewall/policy-rule-sets
                  </a>
                  <br />
                  [2]{" "}
                  <a
                    className="underline"
                    href="https://www.microsoft.com/en-us/download/details.aspx?id=56519"
                    target="_blank"
                    rel="noreferrer"
                  >
                    azure/ip-ranges
                  </a>
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post28;
